

<template>
    <div class="login">
        <div class="container sm:px-10">
            <div class="block xl:grid grid-cols-2 gap-4">
                <!-- BEGIN: Login Info -->
                <div class="hidden xl:flex flex-col min-h-screen">
                    <a href="" class="-intro-x flex items-center pt-5">
                        <img alt="Midone - HTML Admin Template" class="w-6"
                            src="../../assets/admin-assets/dist/images/logo.svg">
                        <span class="text-white text-lg ml-3"> Admin </span>
                    </a>
                    <div class="my-auto">
                        <img alt="Midone - HTML Admin Template" class="-intro-x w-1/2 -mt-16"
                            src="../../assets/admin-assets/dist/images/illustration.svg">
                        <div class="-intro-x text-white font-medium text-4xl leading-tight mt-10">
                            A few more clicks to
                            <br>
                            sign in to your account.
                        </div>
                        <div class="-intro-x mt-5 text-lg text-white text-opacity-70 dark:text-slate-400">Manage all your
                            e-commerce accounts in one place</div>
                    </div>
                </div>
                <!-- END: Login Info -->
                <!-- BEGIN: Login Form -->
                <div class="h-screen xl:h-auto flex py-5 xl:py-0 my-10 xl:my-0">
                    <div
                        class="my-auto mx-auto xl:ml-20 bg-white dark:bg-darkmode-600 xl:bg-transparent px-5 sm:px-8 py-8 xl:p-0 rounded-md shadow-md xl:shadow-none w-full sm:w-3/4 lg:w-2/4 xl:w-auto">
                        <h2 class="intro-x font-bold text-2xl xl:text-3xl text-center xl:text-left">
                            Reset Password
                        </h2>
                        <form method="post" v-on:submit.prevent="resetPassword">
                            <!-- v-on:submit.prevent="loginAdmin"  -->
                            <div class="intro-x mt-8">
                                <div class="relative">
                                    <input type="password" class="intro-x login__input form-control py-3 px-4 block"
                                        :type="showPassword ? 'text' : 'password'" placeholder="Password"
                                        v-model="password">
                                    <div class="input-group-append">
                                        <span
                                            class="material-icons absolute top-1/2 transform -translate-y-1/2 right-2 cursor-pointer"
                                            :class="showPassword ? 'text-gray-600' : 'text-gray-400'"
                                            @click="showPassword = !showPassword">
                                            {{ showPassword ? 'visibility_off' : 'visibility' }}
                                        </span>
                                    </div>
                                    <span v-if="errors && errors.password" class="text-danger">{{ errors.password }}</span>
                                </div>
                            </div>

                            <div class="intro-x mt-8">
                                <input :type="showPasswordConfirmPassword ? 'text' : 'password'"
                                    class="intro-x login__input form-control py-3 px-4 block" placeholder="confirmPassword"
                                    v-model="confirmPassword">
                                <div class="input-group-append">
                                    <span
                                        class="material-icons absolute top-1/2 transform -translate-y-1/2 right-2 cursor-pointer"
                                        :class="showPasswordConfirmPassword ? 'text-gray-600' : 'text-gray-400'"
                                        @click="showPasswordConfirmPassword = !showPasswordConfirmPassword">
                                        {{ showPasswordConfirmPassword ? 'visibility_off' : 'visibility' }}
                                    </span>
                                    <span v-if="errors && errors.confirmPassword" class="text-danger">{{ errors.confirmPassword }}</span>

                                </div>
                            </div>
                            <div class="intro-x mt-5 xl:mt-8 text-center xl:text-left">
                                <button class="btn btn-primary py-3 px-4 w-full xl:w-32 xl:mr-3 align-top"
                                    type="submit">Reset</button>
                            </div>
                        </form>
                    </div>
                </div>
                <!-- END: Login Form -->
            </div>
        </div>
    </div>
    <!-- /.login-box -->
</template>
<script>
import { resetPasswordRequest } from '../../pages/ApiFunctions';
import * as Yup from 'yup';
const schema = Yup.object().shape({
    password: Yup.string().min(6, 'Password must be at least 6 characters').required('Password is required'),
    confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], 'Passwords must match')
    .required('Confirm password is required'),
});
export default {
    name: 'ResetPassword',
    data() {
        return {
            password: "",
            confirmPassword: "",
            token: this.$route.params.token,
            showPassword: false,
            showPasswordConfirmPassword: false,
            errors: {}

        };
    },
    methods: {
        async resetPassword() {
            try {
                await schema.validate({
                    password: this.password,
                    confirmPassword: this.confirmPassword
                }, { abortEarly: false })
                const response = await resetPasswordRequest(`/reset-passwords/${this.token}`, {
                    password: this.password,
                    passwordConfirmation: this.confirmPassword
                });
                this.$router.push('/login');
            }
            catch (error) {
                if (error.name === 'ValidationError') {
                    const formErrors = {}
                    error.inner.forEach((e) => {
                        formErrors[e.path] = e.message
                    })
                    this.errors = formErrors
                    console.log("EROOR=>", this.errors);
                }
            }
        }
    },
}
</script>
<style>
@import '../../assets/admin-assets/dist/css/app.css';
.input-group-append{
    position: relative;
    left: 91%;
    bottom: 35px;
    z-index: 999;
    font-size: 16px;
}
</style>


